.mess {
    &__item {
        margin-bottom: 69px;
    }
    &__bg {
        display: flex;
        justify-content: center;
        align-items: center;
        height: 100%;
        background: rgba( #000, 0.3);
        transition: all 1s;
        &:hover {
            background: rgba( #000, 0.5);
        }
    }
    &__content {
        padding-top: 19px;
    }
    &__inner {
        max-width: 515px;
    }
    &__title {
        margin-bottom: 19px;
        font-family: TeXGyreSchola-Regular;
        font-size: sz(32);
        line-height: sz(43);
        color: $btn-red-1;
    }
    &__text {
        margin-bottom: 19px;
        font-size: sz(14);
        line-height: sz(25);
        color: $black-1;
    }
}

@media (min-width:992px) {
    .mess {
        &__item {
            &:nth-child(odd) {
                .mess__img {
                    margin-right: 12px;
                }
                .mess__content {
                    .mess__inner {
                        margin-left: 38px;
                    }
                }
            }
            &:nth-child(even) {
                .mess__img {
                    order: 2;
                    margin-left: 12px;
                }
                .mess__content {
                    order: 1;
                    display: flex;
                    justify-content: flex-end;
                    .mess__inner {
                        margin-right: 72px;
                    }
                }
            }
        }
        &__img {
            width: 50%;
        }
        &__content {
            width: 50%;
        }
    }
}

@media (max-width:991px) {
    .mess {
        &__item {
            margin-right: 16px;
            margin-left: 16px;
        }
        &__inner {
            margin: auto;
            text-align: center;
        }
    }
}