.hole {
    padding-top: 30px;
    padding-bottom: 30px;
    &__img {
        position: relative;
        margin-bottom: 30px;
    }
    &__img-main {
        width: 90%;
        height: 100%;
    }
    &__img-sub {
        display: flex;
        justify-content: flex-end;
        align-items: center;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        img {
            width: 20%;
        }
    }
    &__content {
        margin-bottom: 30px;
        text-align: center;
    }
    &__title {
        margin-bottom: 20px;
        font-family: TeXGyreSchola-Regular;
        font-size: sz(32);
        line-height: sz(43);
        color: $btn-red-1;
    }
    &__text {
        font-size: sz(14);
        line-height: sz(25);
        color: $bg-back-1;
    }
}

.hole-bar {
    position: relative;
    padding-top: 60px;
    &__item {
        &:hover {
            .hole-bar__text {
                color: $black-1;
            }
            .hole-bar__dot {
                color: $btn-red-2;
            }
        }
    }
    &__text {
        font-size: sz(14);
        text-align: center;
        letter-spacing: sz(0.78);
        color: $grey-1;
        transition: all 0.3s;
    }
    &__dot {
        font-size: sz(20);
        text-align: center;
        color: $grey-2;
        transition: all 0.3s;
    }
    &::before {
        content: '';
        display: block;
        position: absolute;
        width: 100%;
        height: 1px;
        bottom: 15px;
        background-image: linear-gradient(-90deg, rgba(#000, 0) 0%, #CDCAC6 50%, rgba(#000, 0) 100%);
    }
    .center {
        .hole-bar__text {
            font-family: Roboto-Bold;
            color: $black-1;
        }
        .hole-bar__dot {
            color: $btn-red-1;
        }
    }
}

@media (min-width:992px) {
    .hole {
        .col-lg-8 {
            padding-left: 0;
        }
        &__content {
            max-width: 333px;
            text-align: left;
        }
    }
}

@media (min-width: 768px) {
    .hole-banner {
        .banner__content {
            margin-top: 10%;
        }
    }
}