.overflow-hidden {
    overflow: hidden;
}

.overlay {
    background: rgba(#000, 1);
    animation-name: overlay;
    animation-duration: 1s;
    @keyframes overlay {
        0% {
            background: rgba(#000, 0);
        }
        100% {
            background: rgba(#000, 1);
        }
    }
}

.mask {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba( #000, 0.3);
    transition: all 1s;
    &:hover {
        background: rgba( #000, 0.6);
    }
}

.btn-red {
    display: flex;
    justify-content: center;
    align-items: center;
    border: 2px solid $red-2;
    color: #fff;
    background: $red-2;
    transition: all 0.3s;
    &:hover {
        color: $red-2;
        background: #fff;
    }
}

.wrapper {
    background: $bg-grey-1;
}

.zoom-outer {
    overflow: hidden;
    &:hover {
        .zoom-inner {
            transform: scale(1.25)
        }
    }
}

.zoom-inner {
    transition: 0.5s;
}

.line-light-grey {
    display: block;
    width: 100%;
    height: 1px;
    background-image: linear-gradient(-90deg, rgba(201, 230, 254, 0.00) 0%, #CDCAC6 50%, rgba(201, 230, 254, 0.00) 100%);
}