.footer {
    background: $bg-grey-1;
    &__content {
        padding: 22px 30px 0;
        background: #fff;
    }
    &__text1 {
        font-size: sz(14);
        line-height: sz(21);
        color: $black-1;
        &--mini {
            font-size: sz(12);
        }
    }
    &__dev {
        margin-top: 2px;
        margin-bottom: 15px;
        // &--mini {
        //     margin-bottom: 8px;
        // }
    }
    &__logo1 {
        width: 112px;
        margin-left: 11px;
        // &--mini {
        //     width: 85px;
        //     margin-left: 6px;
        // }
    }
    &__logo2 {
        width: 128px;
        margin-bottom: 20px;
    }
    &__text2 {
        margin-bottom: 11px;
        font-size: sz(15);
        line-height: sz(24);
        text-transform: uppercase;
        color: $black-1;
        // &--mini {
        //     margin-bottom: 6px;
        // }
    }
    &__box {
        margin-bottom: 16px;
    }
    &__link {
        font-size: sz(13);
        line-height: sz(24);
        color: $grey-1;
        transition: all 0.3s;
        &:hover {
            text-decoration: underline;
            color: $btn-red-2;
        }
    }
    &__social {
        display: inline-block;
        width: 28px;
        margin-bottom: 13px;
        img {
            width: 100%;
        }
    }
    &__btn-yt {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 154px;
        height: 36px;
        font-size: sz(14);
        color: #fff;
        background: $red-2;
        transition: all 0.3s;
        &:hover {
            color: #fff;
            background: $btn-red-2;
        }
        img {
            margin-right: 8px;
        }
    }
}

@media (max-width:1199px) {
    .footer {
        &__social {
            width: 24px;
        }
    }
}

@media (max-width:991px) {
    .footer {
        &__social {
            width: 28px;
            margin-right: 15px;
        }
    }
}