.getting {
    padding-top: 60px;
    padding-bottom: 30px;
    &__title {
        margin-bottom: 22px;
        font-family: TeXGyreSchola-Regular;
        font-size: sz(32);
        line-height: sz(43);
        text-align: center;
        color: $btn-red-1;
    }
    &__text {
        margin-bottom: 40px;
        font-size: sz(14);
        line-height: sz(25);
        text-align: center;
        text-transform: uppercase;
        color: $black-1;
    }
    &__item {
        margin-bottom: 18px;
    }
    &__blade {
        margin-bottom: 12px;
        font-size: sz(14);
        line-height: sz(25);
        color: $black-1;
        transition: all 0.3s;
        &:hover {
            color: $btn-red-2;
            .getting__line {
                border-color: $btn-red-2;
            }
        }
        .fa-circle {
            margin-right: 8px;
            font-size: sz(11);
            color: $bd-red-1;
        }
        .fa-map-marker {
            margin-right: 8px;
            margin-bottom: 2px;
            font-size: sz(15);
            color: $bd-red-1;
        }
    }
    &__text-first {
        margin-right: 20px;
    }
    &__text-last {
        margin-left: 20px;
    }
    &__line {
        flex: 1;
        border-bottom: 1px solid $grey-2;
        width: 100%;
        transition: all 0.3s;
    }
}