.banner {
    position: relative;
    height: 880px;
    &--md {
        height: 660px;
    }
    &__content {
        position: relative;
    }
    &__logo {
        position: absolute;
        width: 123px;
        top: -75px;
        right: -90px;
    }
    &__text1 {
        font-family: TeXGyreSchola-Regular;
        font-size: sz(20.54);
        line-height: sz(28);
        text-align: center;
        letter-spacing: sz(6.49);
        color: #fff;
    }
    &__text2 {
        margin-bottom: sz(16);
        font-family: TeXGyreSchola-Regular;
        font-size: sz(85);
        line-height: sz(112);
        text-align: center;
        letter-spacing: sz(4.72);
        color: #fff;
        &--about {
            font-size: sz(72);
            line-height: sz(66);
            letter-spacing: sz(-2.9);
        }
    }
    &__text3 {
        padding: 0 16px;
        font-family: TeXGyreSchola-Regular;
        font-size: sz(20.54);
        line-height: sz(28);
        text-align: center;
        letter-spacing: sz(4);
        color: #fff;
        &--about {
            font-family: Roboto-Regular;
            font-size: sz(15);
            letter-spacing: 0;
            text-transform: uppercase;
        }
    }
}

@media (min-width:992px) {
    .banner {
        &__content {
            &--img-golf {
                margin-left: 50%;
            }
            &--designer{
                margin-top: 160px;
            }
        }
    }
}

@media (max-width: 991px) {
    .banner {
        height: 680px;
        &--md {
            height: 630px;
        }
        &__logo {
            top: -75px;
            right: -50px;
        }
    }
}

@media (max-width: 767px) {
    .banner {
        &__logo {
            width: 100px;
            top: 210px;
            right: 50%;
            margin-right: -50px
        }
    }
}

@media (max-width: 575px) {
    .banner {
        height: 580px;
        &--md {
            height: 450px;
        }
        &__logo {
            top: 180px;
        }
        &__text1 {
            font-size: sz(16);
        }
        &__text2 {
            font-size: sz(44);
            line-height: sz(60);
        }
        &__text3 {
            font-size: sz(16);
        }
    }
}