.rate {
    padding-top: 60px;
    padding-bottom: 36px;
    &__title {
        margin-bottom: 20px;
        font-family: TeXGyreSchola-Regular;
        font-size: sz(32);
        color: $btn-red-1;
        letter-spacing: 0;
        line-height: sz(43);
    }
    &__text {
        margin-bottom: 20px;
        font-size: sz(14);
        line-height: sz(25);
        letter-spacing: 0;
        color: $black-1;
    }
    &__btn {
        width: 205px;
        height: 50px;
        font-size: sz(16);
        letter-spacing: sz(0.51);
    }
}

@media (min-width: 768px) {
    .rate-banner {
        .banner__content {
            margin-top: 10%;
        }
    }
}