.night-banner {
    .mask {
        background: transparent;
        &:hover {
            background: rgba( #000, 0.3);
        }
    }
}
.night-info {
    padding: 60px 0 38px;
}

.night-slide {
    position: relative;
    margin-bottom: 60px;
    .owl-nav {
        display: flex;
        flex-direction: row-reverse;
        justify-content: space-between;
        position: absolute;
        width: 100%;
        left: 0;
        top: 50%;
        margin-top: -25px;
    }
    .fa {
        padding: 0 25px;
        font-size: 50px;
        color: #fff;
    }
}

@media (min-width: 768px) {
    .night-banner {
        .banner__content {
            margin-top: 10%;
        }
    }
}