.home-wlc {
    &__wrap {
        position: relative;
        height: 610px;
        width: 945px;
        margin: auto;
    }
    &__rec {
        position: absolute;
        top: 93px;
        right: 0;
        width: 877px;
        height: 454px;
        border: 3px solid $bd-red-1;
    }
    &__img {
        position: absolute;
        top: 111px;
        left: 0;
        width: 673px;
        height: 418px;
    }
    &__content {
        position: absolute;
        top: 60px;
        right: 29px;
        width: 457px;
        height: 520px;
        padding: 60px 30px 56px;
        background: #fff;
    }
    &__text1 {
        margin-bottom: 19px;
        font-family: TeXGyreSchola-Regular;
        font-size: sz(32);
        line-height: sz(43);
        color: $bg-back-1;
    }
    &__line {
        width: 44px;
        height: 2px;
        margin-bottom: 28px;
        background: $red-1;
    }
    &__box {
        width: 397px;
        height: 240px;
        overflow: hidden;
        margin-bottom: 20px;
    }
    &__text2 {
        margin-bottom: 20px;
        font-size: sz(14);
        line-height: sz(24);
        color: $bg-back-1;
        letter-spacing: 0.44px;
    }
    &__more {
        width: 139px;
        height: 50px;
        font-size: sz(16);
        color: #fff;
    }
}

.home-info {
    padding-top: 30px;
    &__item {
        margin-bottom: 30px;
        padding: 18px;
        background: #fff;
    }
    &__box {
        width: 50%;
        margin: 12px;
    }
    &__text1 {
        margin-bottom: 14px;
        font-family: TeXGyreSchola-Regular;
        font-size: sz(32);
        line-height: sz(43);
        color: $btn-red-1;
    }
    &__text2 {
        margin-bottom: 19px;
        font-size: sz(14);
        line-height: sz(20);
        letter-spacing: sz(0.44);
        color: $bg-back-1;
    }
    &__text3 {
        max-height: 120px;
        overflow: hidden;
        font-family: Roboto-Bold;
        font-size: 14px;
        line-height: sz(20);
        letter-spacing: sz(0.44);
        color: $red-2;
        &:hover {
            text-decoration: underline;
            color: $btn-red-2;
        }
    }
}

.home-video {
    position: relative;
    height: 600px;
    margin: 30px 0;
    &__play {
        margin-top: 8px;
        margin-right: 19px;
        margin-bottom: 20px;
    }
    &__text1 {
        font-family: HelveticaNeueRegular;
        font-size: sz(55);
        font-weight: bold;
        text-align: center;
        letter-spacing: sz(1.57);
        text-shadow: 0 4px 2px rgba(0, 0, 0, 0.73);
        color: #fff;
    }
    &__text2 {
        font-family: HelveticaNeueRegular;
        font-size: sz(36);
        font-weight: bold;
        text-align: center;
        letter-spacing: sz(1.57);
        text-shadow: 0 4px 2px rgba(0, 0, 0, 0.73);
        color: #fff;
    }
}

.home-learn {
    padding-top: 30px;
    padding-bottom: 30px;
    background: $bg-grey-1;
    &__content {
        padding-top: 37px;
        background: #fff;
    }
    &__title {
        margin-bottom: 37px;
        font-family: TeXGyreSchola-Regular;
        font-size: sz(32);
        line-height: sz(43);
        text-align: center;
        color: $bg-back-1;
    }
    &__item {
        width: (100%)/3;
        padding: 60px 40px;
        text-align: center;
        &:nth-child(1) {
            border-top: 2px solid $bg-grey-1;
            border-right: 1px solid $bg-grey-1;
            border-bottom: 2px solid transparent;
            border-left: 2px solid transparent;
        }
        &:nth-child(2) {
            border-top: 2px solid $bg-grey-1;
            border-right: 2px solid $bg-grey-1;
            border-bottom: 2px solid transparent;
            border-left: 2px solid $bg-grey-1;
        }
        &:nth-child(3) {
            border-top: 2px solid $bg-grey-1;
            border-right: 2px solid transparent;
            border-bottom: 2px solid transparent;
            border-left: 1px solid $bg-grey-1;
        }
    }
    &__img {
        display: flex;
        align-items: center;
        width: 80px;
        height: 80px;
        margin: 0 auto 32px;
        img {
            width: 100%;
        }
    }
    &__text1 {
        margin-bottom: 22px;
        font-family: TeXGyreSchola-Regular;
        font-size: sz(28);
        line-height: sz(35);
        letter-spacing: sz(1.24);
        color: $black-1;
    }
    &__text2 {
        height: 87px;
        overflow: hidden;
        margin-bottom: 30px;
        font-size: sz(15);
        line-height: sz(22);
        color: $grey-1;
    }
    &__text3 {
        font-size: sz(14);
        line-height: sz(24);
        letter-spacing: sz(1.05);
        color: $red-2;
        &:hover {
            text-decoration: underline;
            color: $btn-red-2;
        }
    }
}

@media (max-width: 991px) {
    .home-wlc {
        &__wrap {
            width: 100%;
        }
        &__rec {
            width: 90%;
        }
        &__img {
            width: 90%;
        }
    }
    .home-learn {
        &__item {
            width: 80%;
            margin-bottom: 30px;
            &:nth-child(1),
            &:nth-child(2),
            &:nth-child(3) {
                border: 2px solid $bg-grey-1;
            }
        }
    }
}

@media (max-width: 575px) {
    .home-wlc {
        &__content {
            right: 0;
            width: 100%;
        }
        &__box {
            width: auto;
        }
        &__text1 {
            font-size: 24px;
            white-space: pre-wrap;
        }
    }
    .home-info {
        &__box {
            width: 100%;
        }
    }
    .home-video {
        height: 400px;
        &__text1 {
            font-size: sz(32);
        }
        &__text2 {
            font-size: sz(24);
        }
    }
    .home-learn {
        &__item {
            width: 95%;
        }
    }
}