.accommodation {
	&__text {
		font-size: 14px;
		width: 50%;
		padding-right: 25px;
		ul {
			padding-left: 20px;
			li {
				padding-left: 10px;
			}
		}
	}
	&__desc {
		color: $btn-red-1;
	}
	&__head {
		font-family: Roboto-Bold;
		color: $black-1;
		margin-bottom: 10px;
	}
	&__link {
		color: $btn-red-1;
		&:hover {
            color: $btn-red-2;
        }
	}
}

@media (max-width: 991px) {
	.accommodation {
		&__text {
			width: 100%;
			max-width: 515px;
			margin: auto;
		}
	}
}