.header {
    position: relative;
    background-color: $bg-back-1;
    &__logo {
        width: 46px;
        height: 17px;
        margin: 14px 30px 11px;
    }
    &__link {
        a {
            margin-right: sz(24);
            font-size: sz(11);
            line-height: sz(18);
            color: #FFFFFF;
            opacity: 0.62;
            transition: all 0.3s;
            &:hover {
                opacity: 1;
            }
        }
    }
    &__lan {
        display: inline-block;
        margin-right: 24px;
        margin-bottom: 2px;
    }
    &__book {
        padding: sz(14) sz(45);
        color: #FFFFFF;
        font-size: sz(12);
        line-height: sz(14);
        background: $btn-red-1;
        transition: all 0.3s;
        &:hover {
            color: #fff;
            background: $btn-red-2;
        }
    }
}

.header-outer {
    position: absolute;
    z-index: 10;
    width: 100%;
    height: 0;
    text-align: center;
    &__logo {
        margin-bottom: 40px;
    }
    &__menu {
        display: flex;
        flex-direction: column;
        a {
            margin-bottom: 20px;
            font-family: TeXGyreSchola-Regular;
            font-size: sz(28);
            line-height: sz(43);
            color: rgba(#fff, 0.7);
            transition: all 0.3s;
            &:hover {
                color: rgba(#fff, 1);
            }
        }
    }
    .hamburger {
        display: flex;
        align-items: center;
        position: absolute;
        top: 32px;
        right: 44px;
        padding: 0;
    }
    .hamburger-box {
        width: 24px;
        height: 22px;
    }
    .hamburger-inner,
    .hamburger-inner:after,
    .hamburger-inner:before {
        width: 24px;
        height: 2px;
        background: #fff;
    }
    .is-active {
        .header-outer__text {
            display: none;
        }
    }
    &__text {
        margin-right: 11px;
        font-size: sz(17);
        color: #fff;
    }
}

@media (max-width: 575px) {
    .header {
        &__link {
            margin-bottom: 20px;
        }
        &__action {
            margin-bottom: 20px;
        }
    }
    .header-outer {
        &__logo {
            width: 100px;
            margin-bottom: 24px;
        }
        &__menu {
            a {
                margin-bottom: 16px;
                font-size: sz(18);
                line-height: sz(24);
            }
        }
        .hamburger {
            top: 24px;
            right: 16px;
        }
    }
}