.designer-info {
    padding: 60px 0 8px;
}

.designer {
    padding-top: 30px;
    padding-bottom: 30px;
    img {
        width: 232px;
        margin-bottom: 22px;
    }
    &__text {
        margin-bottom: 22px;
        font-size: sz(14);
        line-height: sz(25);
        color: $black-1;
    }
    .col-lg-4 {
        margin-bottom: 30px;
    }
}

@media (min-width:992px) {
    .designer {
        &__content {
            max-width: 340px;
        }
    }
}
@media (max-width:991px) {
    .designer {
        &__content {
            text-align: center;
        }
    }
}