.img-golf-about {
    padding-top: 60px;
    padding-bottom: 30px;
    img {
        width: 100%;
        border-radius: 20px;
    }
    &__content{
        max-width: 515px;
        margin: auto;
        padding-top: 22px;
        padding-bottom: 22px;
    }
    &__title {
        margin-bottom: 22px;
        font-family: TeXGyreSchola-Regular;
        font-size: sz(32);
        line-height: sz(43);
        color: $btn-red-1;
    }
    &__text {
        margin-bottom: 22px;
        font-size: sz(14);
        line-height: sz(25);
        color: $black-1;
    }
    &__info{
        max-width: 650px;
        margin: auto;
        padding-top: 60px;
        padding-bottom: 30px;
        text-align: center;
    }
    &__btn{
        width: 206px;
        height: 50px;
        margin: auto;
        font-size: sz(16);
        text-transform: uppercase;
        letter-spacing: sz(0.51);
    }
}

@media (max-width: 991px) {
    .img-golf-about {
        text-align: center;
    }
}