.course-slide {
    height: 100%;
    .carousel-inner {
        position: relative;
        height: 100%;
        &::before {
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            z-index: 1;
            width: 100%;
            height: 100%;
            background: rgba(#000, 0.3)
        }
    }
    .carousel-item {
        height: 100%;
    }
}

.course-info {
    padding-top: 60px;
    padding-bottom: 18px;
    border-bottom: 1px solid $grey-3;
}

.course-fe {
    padding-top: 30px;
    border-bottom: 1px solid $grey-3;
    &__item {
        margin-bottom: 30px;
    }
    &__img {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 97px;
        min-width: 97px;
        height: 97px;
        margin-right: 15px;
        background: $btn-red-1;
        img {
            max-width: 80%;
            max-height: 80%;
        }
    }
    &__text1 {
        margin-bottom: 9px;
        a {
            font-family: TeXGyreSchola-Regular;
            font-size: sz(24);
            line-height: sz(32);
            color: $btn-red-1;
            &:hover {
                color: $btn-red-2;
            }
        }
    }
    &__text2 {
        font-size: sz(14);
        line-height: sz(18);
        letter-spacing: sz(0.78);
        color: $black-1;
    }
}

.course-fu {
    padding-top: 60px;
    padding-bottom: 30px;
    &__item {
        margin-bottom: 30px;
    }
    &__mask {
        width: 100%;
        height: 100%;
        background-image: linear-gradient(90deg, rgba(#000, 0.75) 15%, rgba(#fff, 0) 100%);
    }
    &__blade {
        margin-left: 40px;
        img {
            max-width: 40px;
            max-height: 40px;
            margin-bottom: 13px;
        }
    }
    &__text1 {
        font-size: sz(16);
        a {
            color: #FFF;
            transition: all 0.3s;
            &:hover {
                color: $btn-red-2;
            }
        }
    }
    &__banner {
        height: 500px;
        margin-bottom: 30px;
    }
    &__banner-content {
        max-width: 635px;
    }
    &__text2 {
        margin-bottom: 20px;
        font-family: TeXGyreSchola-Regular;
        font-size: sz(40);
        line-height: sz(54);
        color: #FFF;
    }
    &__text3 {
        margin-bottom: 30px;
        font-size: sz(18);
        line-height: sz(25);
        color: #FFF;
    }
    &__btn {
        width: 205px;
        height: 50px;
        margin: auto;
        font-size: sz(16);
        line-height: sz(22);
        letter-spacing: sz(0.51);
        color: #FFF;
        &:hover {
            border-color: $btn-red-2;
            color: #fff;
            background: $btn-red-2;
        }
    }
}

@media (min-width: 768px) {
    .course-slide {
        .carousel-indicators {
            display: flex;
            flex-direction: column;
            align-items: flex-end;
            bottom: 50%;
            left: auto;
            z-index: 2;
            margin: 0;
            margin-bottom: -138px;
            li {
                width: 120px;
                height: 90px;
                margin: 2px 0 0;
                transition: all 0.5s;
                &.active {
                    width: 150px;
                }
            }
        }
    }
}

@media (max-width:767px) {
    .course-slide {
        .carousel-indicators {
            bottom: 10%;
            z-index: 2;
            li {
                width: 50px;
                height: 5px;
                background: rgba($grey-1, 0.7) !important;
                transition: all 0.5s;
                &.active {
                    background: rgba($green-1, 0.7) !important;
                }
            }
        }
    }
}

@media (max-width:575px) {
    .course-fu {
        &__blade {
            margin-left: 20px;
        }
        &__text2 {
            font-size: sz(30);
            line-height: sz(44);
        }
        &__text3 {
            font-size: sz(16);
            line-height: sz(24);
        }
        &__btn {
            width: 180px;
            height: 40px;
            margin: auto;
            font-size: sz(14);
        }
    }
}